.intro{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;height:52rem;margin:auto;margin-left:10rem;margin-top:10rem}.intro .hello{font-family:"Montserrat", sans-serif;margin:2rem 2rem;margin-bottom:0%;font-weight:900}.intro .hello .name:hover{color:#fa5252;cursor:pointer}.intro .desc{color:#343a40;margin:0rem 2rem 2rem 2rem;width:450px}.intro .tag{color:#343a40;margin:0rem 2rem 2rem 2rem;width:450px;color:#be4bdb}.intro .typing{margin:0rem 2rem 2rem 2rem;color:#343a40;font-family:"Roboto Mono", monospace}.intro .down{font-size:4.5rem;margin:2rem;color:#495057}@media (max-width: 570px){.intro{margin:1rem}.intro .desc{color:#343a40;margin:0rem 2rem 2rem 2rem;width:80%}.intro .tag{width:70%}}.navbar{height:10rem;display:-webkit-flex;display:flex;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-justify-content:flex-end;justify-content:flex-end;-webkit-align-items:center;align-items:center}.navbar Button{margin:auto;margin:0rem 5rem;background-color:#be4bdb;border-radius:8px;padding:1rem 2rem;color:#212529;font-size:2.5rem;font-weight:900;cursor:pointer;font-family:"Montserrat", sans-serif;border:1px solid #aa29cb;border-bottom:3px solid #aa29cb;box-shadow:0px 12px 24px rgba(18,32,43,0.26)}.development{margin:0px;height:52rem}.development .dev{width:300px;margin:auto;margin:auto;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-evenly;justify-content:space-evenly}.development .dev .icon{color:#ff922b;text-align:center;margin-top:1rem}.development .dev .title{color:#212529;margin-left:2rem}.development .projects .pro{width:100%;margin:auto;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-evenly;justify-content:space-evenly;-webkit-flex-wrap:wrap;flex-wrap:wrap;margin-bottom:6rem;margin-top:5rem}.development .projects .pro :hover{box-shadow:3px 8px 15px #495057;-webkit-transform:translateX(2rem, 2rem);transform:translateX(2rem, 2rem)}.development .projects .pro .preview{display:inline-block;width:450px;height:250px;border:3px solid #6c757d;border-radius:8px;cursor:pointer;margin-bottom:2rem}@media (max-width: 690px){.development .projects .pro .preview{width:80%;height:250px}}.footer{height:50rem;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:flex-end;justify-content:flex-end}.footer .contact{position:absolute;left:50%;-webkit-transform:translateX(-50%);transform:translateX(-50%);height:300px;width:80%;margin:auto;background-color:#f8f9fa;border-radius:13px;margin-bottom:10rem;box-shadow:3px 10px 24px #212529;font-family:"Roboto" sans-serif;text-align:center;font-weight:600}.footer .contact .enveloped{display:inline-block;margin-top:3rem;color:#343a40}.footer .contact .enveloped :hover{color:#d6336c;cursor:pointer}.footer .contact h1{font-size:3rem;margin:1rem;color:#343a40}.footer .contact h3{font-size:2rem;margin:2rem;color:#495057}.footer .contact h3 a{text-decoration:none;color:#212529;font-weight:900 !important;font-size:2rem !important}.footer .contact h3 a :hover{color:#be4bdb;cursor:pointer}.footer .contact .social{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-flex-wrap:wrap;flex-wrap:wrap;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-evenly;justify-content:space-evenly;width:130px;margin:auto}.footer .contact .social .envelope{display:inline-block;margin:10px;color:#343a40}.footer .contact .social .envelope :hover{color:#d6336c;cursor:pointer}.footer .rights{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center;-webkit-justify-content:flex-end;justify-content:flex-end;padding-bottom:2rem;width:100%;height:20rem;background-color:#be4bdb;box-shadow:3px 10px 24px #212529}.footer .rights .copy{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction